<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
  .store-check-wraper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .check-store-item {
    position: relative;
    border: 1px solid #ececec;
    border-radius: 6px;
    padding: 0 20px 0 12px;
    margin: 10px 15px 0 0;
    height: 36px;
    line-height: 36px;
    background: #ececec;
  }
</style>

<template>
  <div class="add-form-wraper">
    <el-form ref="form" :model="addParamMesg" label-width="160px">
      <el-form-item label="选择城市" required>
        <div>{{addParamMesg.cityName}}</div>
      </el-form-item>

      <el-form-item label="预售周期名称" required>
        <div>{{addParamMesg.activityName}}</div>
      </el-form-item>

      <el-form-item label="起止时间" required>
        <div>
          {{addParamMesg.startDate}} - {{addParamMesg.endDate}}
        </div>
      </el-form-item>

      <el-form-item label="提货日期" required>
        <div>
          {{addParamMesg.pickUpStartDate}} - {{addParamMesg.pickUpEndDate}}
        </div>
      </el-form-item>
      <el-form-item label="参与门店" required>
        <div v-if="activityStoresCheckedList.length > 0" class="store-check-wraper">
          <div v-for="(item, idx) in activityStoresCheckedList" :key="idx" class="check-store-item">
            {{item.erpStoreNo}}_{{item.erpStoreName}}({{item.saleTypeName}})
          </div>
        </div>
      </el-form-item>
      <el-form-item label="设置预售商品" required>
        <div>进行中不可添加/修改商品</div>
        <el-table
          class="fixed-height"
          :data="checkedList"
          empty-text="暂无选中任何商品"
          style="width:100%;"
          size="small">
          <el-table-column width="226px" label="商品信息">
            <template slot-scope="scope">
              <div class="ll-p-m-w">
                <div class="ll-img-w">
                  <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                  <img v-else src="/shop/static/img_error.png" alt="">
                </div>
                <div class="ll-p-m-c-r">
                  <div>
                    {{scope.row.goodsName}}
                  </div>
                  <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                  <div class="ll-erp-p-no" v-if="scope.row.platformCityId">商品ID：{{scope.row.platformCityId || '--'}}</div>
                </div>
              </div>
            </template> 
          </el-table-column>
          <el-table-column width="120px" prop="spec" label="规格"></el-table-column>
          <el-table-column width="130px" label="原价(必填)">
            <template slot-scope="scope">
              <div>
                {{scope.row.price}}
              </div>
            </template>
          </el-table-column>
          <el-table-column width="130px" label="预售价(必填)">
            <template slot-scope="scope">
              <div>
                {{scope.row.presalePrice}}
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="130px" :render-header="platformCurStockHeader">
            <template slot-scope="scope">
              <div>
                {{scope.row.platformCurStock}}
              </div>
            </template>
          </el-table-column>
          <el-table-column width="130px" label="门店限售库存">
            <template slot-scope="scope">
              <div>
                {{scope.row.stock}}
              </div>
            </template>
          </el-table-column>
          <el-table-column width="160px" prop="marketPrice" label="每人限购(为空时不限)">
            <template slot-scope="scope">
              <div>
                {{scope.row.memberLimitNum}}
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="操作"
            width="100">
            <template slot-scope="scope" size="small">
              <div class="shareline" v-clipboard:copy="scope.row.copyUrl" v-clipboard:success="onCopySuc" v-clipboard:error="onCopyError">
                 <el-button type="text" size="mini" icon="el-icon-document">复制链接</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </el-form-item>
      
      <hr class="s-slip-tb">
      <el-form-item>
        <el-button size="small" @click="doCancelHandel">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  const presaleGoodsMgt = {
    detail: '/plateform/a/mall/saleActivity/detail'
  }
  export default {
    data() {
      return {
        activityId: '', // 活动id
        isDisabled: false,
        addParamMesg: {
          platformId: 5, // 平台类型：1-有赞；2-京东；3-美团；4-饿百；5-商城
          cityId: '', // 城市id
          cityName: '', // 城市名称
          activityName: '', // 活动名称
          startDate: '', // 开始时间
          endDate: '', // 结束时间
          pickUpStartDate: '', // 提货开始时间
          pickUpEndDate: '', // 提货结束时间
          goodsReqList: []
        },
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7
          }
        },
        // 区域城市列表
        cityOptions: [],
        // 选中的商品列表
        checkedList: [],
        // 参与门店选中的
        activityStoresCheckedList: []

      }
    },
    created() {
      this.activityId = this.$route.params.id
      // 获取详情
      this.getDetailMesg()
    },
    methods: {
      // 获取详情
      getDetailMesg() {
        utils.showLoading()
        this.$http.post(presaleGoodsMgt.detail, {activityId: this.activityId}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              this.addParamMesg.activityName = res.data.activityName
              this.addParamMesg.cityName = res.data.cityName
              this.addParamMesg.startDate = res.data.startDate
              this.addParamMesg.endDate = res.data.endDate
              this.addParamMesg.pickUpStartDate = res.data.pickUpStartDate
              this.addParamMesg.pickUpEndDate = res.data.pickUpEndDate
              if(res.data.storeNoList && res.data.storeNoList.length) {
                this.activityStoresCheckedList = res.data.storeNoList
              }
              if(res.data.goodsVoList && res.data.goodsVoList.length > 0) {
                for(let item of res.data.goodsVoList) {
                  item.copyUrl = `pages/product/detail?id=${item.platformCityId}&type=3`
                }
                this.checkedList = res.data.goodsVoList
              }else {
                this.checkedList = []
              } 
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doCancelHandel 返回]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$router.push('/long_pgm')
      },
      /**
       * [copyUrl 复制链接成功]
       * @return {[type]}      [description]
       */
      onCopySuc() {
        this.$message.success("链接已复制至剪切板，仅限微信公众号中使用")
      },
      /**
       * [onCopyError 复制链接失败]
       * @return {[type]} [description]
       */
      onCopyError() {
        this.$message.error("复制链接失败,请手动复制")
      },
      platformCurStockHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover">
            <div>
              <p>区域限售库存，即所有门店共享的总预售库存；</p>
              <p>门店限售即每个门店的预售库存，可在门店预售商品中调整门店库存；</p>
              <p>每个商品至少要设置一种限售库存方式，最大可设置9999；</p>
              <p>如果既设了区域限售，又设了门店限售，则先校验区域库存，再校验门店库存；</p>
            </div>
            <span slot="reference">区域限售库存<i class="el-icon-question"></i></span>
          </el-popover>
        )
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }

  }
</script>


